  /**
   * Esta funo  responsvel por enviar um novo ticket de chamado.
   *
   * @param {Event} event - O evento associado ao envio do formulrio ou  ao que aciona a funo.
   * @returns {void} - Esta funo no retorna nada diretamente, mas realiza o envio do ticket de chamado.
   */
  function submitTicket(event) {
    event.preventDefault();
    /**
     * Dentro da funo 'submitTicket', so realizadas validaes para verificar se determinados campos foram preenchidos antes de enviar o ticket de chamado.
     * Se algum campo estiver vazio, um alerta  exibido indicando o problema especfico.
     *
     * @param {String} respectiveArea - Uma String representando a rea responsvel pelo chamado.
     * @param {String} sector - Uma String representando o tipo de ocorrncia.
     * @param {String} occurrence - Uma String representando o tipo de problema.
     * @param {String} problemn - Uma String representando o problema especfico.
     * @param {String} setAlertVerify - Uma String para definir o estado do alerta de validao.
     * @param {String} setMessagetitle - Uma String para definir o ttulo da mensagem de alerta.
     * @returns {void} - Esta funo no retorna nada diretamente, mas exibe alertas se os campos necessrios no forem preenchidos.
     */
    if (respectiveArea.length === 0) {
      setAlertVerify(true);
      setMessagetitle("Selecione a rea Responsvel pelo Chamado");
      return;
    } else if (sector.length === 0) {
      setAlertVerify(true);
      setMessagetitle("Selecione um tipo de ocorrencia");
      return;
    } else if (occurrence.length === 0) {
      setAlertVerify(true);
      setMessagetitle("Selecione um tipo de problema");
      return;
    } else if (problemn.length === 0) {
      setAlertVerify(true);
      setMessagetitle("Selecione o problema em especifico");
      return;
    } else {
      setAlertVerify(false);
    }
    // * Caso nenhuma opo seja selecionada mostra mensagem e no cria o chamado

    // Obter o dia, ms e ano da dataUser atual
    var dataUserAtual = new Date();
    var dia = dataUserAtual.getDate();
    var mes = dataUserAtual.getMonth() + 1; // Os meses em JavaScript so indexados a partir de zero, por isso  necessrio adicionar 1
    var ano = dataUserAtual.getFullYear();

    function adicionaZero(numero) {
      if (numero < 10) {
        return "0" + numero;
      }
      return numero;
    }
    var horaFormatada = adicionaZero(dataUserAtual.getHours()) + ":" + adicionaZero(dataUserAtual.getMinutes());

    // Formatar a dataUser no formato dd/mm/yy

    // Formatando para dataUser BR
    var dataUserFormatada = ano + "-" + ("0" + mes).slice(-2) + "-" + ("0" + dia).slice(-2) + " " + horaFormatada;

    /**
     * Inicializao de variveis dentro do escopo da funo ou bloco de cdigo.
     *
     * @type {string} Status - Varivel para armazenar o status do ticket de chamado.
     * @type {Array} NewDatesAlocate - Array vazio para armazenar novas datas alocadas.
     * @type {FormData} formdataUser - Objeto FormData para coletar dados de formulrio.
     * @type {number} total_size - Varivel para armazenar o tamanho total, inicializada com zero.
     */
    let Status;
    let NewDatesAlocate = [];
    const formdataUser = new FormData();
    var total_size = 0;

    if (filename.length > 0) {
      for (let i = 0; i < fileimg.length; i++) {
        const file = fileimg[i];
        total_size += file.size;
        formdataUser.append("image", file);
      }
    } else if (daysForAlocate.length > 0) {
      for (let dateObj of daysForAlocate) {
        const day = dateObj.getDate().toString().padStart(2, "0");
        const month = (dateObj.getMonth() + 1).toString().padStart(2, "0");
        const year = dateObj.getFullYear();
        const dateFormated = `${year}-${month}-${day}`;
        NewDatesAlocate.push(dateFormated);
      }
      formdataUser.append("id_equipament", equipamentSelected);
      formdataUser.append("days_alocated", NewDatesAlocate);
    } else if (problemn === "Criacao de usuario de rede") {
      if (newname.length < 2) {
        setMessage(true);
        setTypeError("Falta de dados");
        setMessageError("Nome Obrigatrio!!!");
        return;
      }

      if (sectornewuser.length < 1) {
        setMessage(true);
        setTypeError("Falta de dados");
        setMessageError("Setor Obrigatrio!!!");
        return;
      }

      if (motivationContract.length < 2) {
        setMessage(true);
        setTypeError("Falta de dados");
        setMessageError("Tipo da Contratao Obrigatria!!!");
        return;
      }

      if (necessaryMachine === undefined) {
        setMessage(true);
        setTypeError("Falta de dados");
        setMessageError("Obigatrio informar a necessidade de Mquina!!!");
        return;
      }

      if (companynewUser.length < 2) {
        setMessage(true);
        setTypeError("Falta de dados");
        setMessageError("Obigatrio informar a Unidade!!!");
        return;
      }

      if (centralcost.length < 2) {
        setMessage(true);
        setTypeError("Falta de dados");
        setMessageError("Obigatrio Informar Centro de Custo!!!");
        return;
      }

      if (jobtitlenewuser.length < 2) {
        setMessage(true);
        setTypeError("Falta de dados");
        setMessageError("Obigatrio Informar o cargo!!!");
        return;
      }
      formdataUser.append("new_user", newname);
      formdataUser.append("sector_new_user", sectornewuser);
      formdataUser.append("where_from", motivationContract);
      formdataUser.append("machine_new_user", necessaryMachine);
      formdataUser.append("company_new_user", companynewUser);
      formdataUser.append("software_new_user", softwareNewUser);
      formdataUser.append("cost_center", centralcost);
      formdataUser.append("job_title_new_user", jobtitlenewuser);
      formdataUser.append("start_work_new_user", selectedDay);
      formdataUser.append("copy_profile_new_user", copyUser);
    } else if (problemn === "Exclusao de usuario de rede") {
      if (newname.length < 2) {
        setMessage(true);
        setTypeError("Falta de dados");
        setMessageError("Nome Obrigatrio!!!");
        return;
      }
      if (maildelegation.length < 2) {
        setMessage(true);
        setTypeError("Falta de dados");
        setMessageError("E-mail para transferencia Obrigatrio!!!");
        return;
      }
      if (dirsave.length < 2) {
        setMessage(true);
        setTypeError("Falta de dados");
        setMessageError("Obrigatrio informar onde os dados devem ser salvos!!!");
        return;
      }
      formdataUser.append("new_user", newname);
      formdataUser.append("mail_tranfer", maildelegation);
      formdataUser.append("old_files", dirsave);
      formdataUser.append("start_work_new_user", selectedDay);
    } else {
      formdataUser.append("start_date", dataUserFormatada);
      formdataUser.append("PID", dataUser.pid);
      formdataUser.append("respective_area", respectiveArea);
    }
    if (total_size > 10 * 1024 * 1024) {
      setMessage(true);
      setTypeError("Capacidade Mxima Ultrapassada");
      setMessageError("Capacidade Mxima de Arquivos Anexado  de 20MB");
      return;
    }
    formdataUser.append("ticketRequester", dataUser.name);
    formdataUser.append("department", dataUser.departament);
    formdataUser.append("mail", dataUser.mail);
    formdataUser.append("company", dataUser.company);
    formdataUser.append("sector", sector);
    formdataUser.append("occurrence", occurrence);
    formdataUser.append("problemn", problemn);
    if (observation.length < 2) {
      setMessageError("Obrigatrio Escrever Obversao conforme o chamado");
      setTypeError("Falta de Dados");
      setMessage(true);
      return;
    }
    formdataUser.append("observation", observation);
    formdataUser.append("start_date", dataUserFormatada);
    formdataUser.append("PID", dataUser.pid);
    formdataUser.append("respective_area", respectiveArea);
    fetch("submitTicket/", {
      method: "POST",
      headers: {
        "X-CSRFToken": csrfToken,
        "Cache-Control": "no-cache",
      },
      body: formdataUser,
    })
      .then((response) => {
        Status = response.status;
        return response.json();
      })
      .then((dataUser) => {
        if (Status === 403) {
          setMessage(true);
          setTypeError("Falta de dados");
          setMessageError("PID No cadastrado, favor contatar equipe de TI");
          return window.scrollTo({
            top: 0,
            behavior: "smooth",
          });
        } else if (Status === 200) {
          setInfoID(dataUser.id);
          setInfo(true);
          setInfoClass("animate__lightSpeedInRight");
          selectAR.current.selectedIndex = 0;
          observationRef.current.value = "";
          setInfoClass2("closeInfo");
          setTimeout(() => {
            setInfo(false);
          }, 6000);
          setInfra(false);
          setBackup(false);
          setAlert(false);
          setMail(false);
          setEquip(false);
          setUser(false);
          setFormNewUser(false);
          setFormDelUser(false);
          setInternet(false);
          setFolder(false);
          setSYS(false);
          setSYS2(false);
          setAlocate(false);
          setDateEquip(false);
          setDados(false);
          setSoftAPP(false);
          setRespectiveTI(false);
        } else if (Status === 320) {
          setMessage(true);
          setTypeError("Dados Invlidos");
          setMessageError("Arquivo Anexado Invlido");
          return window.scrollTo({
            top: 0,
            behavior: "smooth",
          });
        } else if (Status === 310) {
          setDaysForAlocate([]);
          const dates = dataUser.dates;
          var btn = document.querySelectorAll(".rdp-day");
          btn.forEach(function (b) {
            for (var i = 0; i < dates.length; i++) {
              var date = dates[i].slice(-2);
              if (b.textContent === date) {
                b.classList.remove("rdp-day_selected");
                b.disabled = true;
              }
            }
          });

          setMessage(true);
          setTypeError("Dados Invlidos");
          setMessageError("dataUser de Alocao Indisponivel");
          return window.scrollTo({
            top: 0,
            behavior: "smooth",
          });
        }
      })
      .catch((err) => {
        return console.log(err);
      });
  }

  function closeMessage() {
    setMessage(false);
  }

  function inputDrop() {
    setInputDropControl(true);
    setInputManualControl(false);
    file_name = fileimg.map((fileItem) => fileItem.name);
    setFileName(file_name);

    const paragraphs = file_name.map((fileName, index) => (
      <DivNameFile>
        <PNameFile key={index} className="text-break">
          {fileName}
        </PNameFile>
        <div>
          {(() => {
            const file = fileimg[index];
            const sizeInBytes = file.size;
            let size;
            let unit;

            if (sizeInBytes >= 1024 * 1024) {
              size = sizeInBytes / (1024 * 1024);
              unit = "MB";
            } else {
              size = sizeInBytes / 1024;
              unit = "KB";
            }

            return `${size.toFixed(2)} ${unit}`;
          })()}
        </div>
        <BtnFile
          type="button"
          onClick={() => {
            fileimg.splice(index, 1);
            inputDrop();
            const divider = document.getElementById("divider");
            divider.classList.remove("lineTop");
          }}
        >
          <ImgFile src={Exclude} alt="Excluir arquivo" />
        </BtnFile>
      </DivNameFile>
    ));

    const Div = <div className="w-100">{paragraphs}</div>;

    setNameOnDropFiles(Div);
    setFileSizeNotify(true);
  }

  function inputManual(event) {
    // setInputDropControl(false);
    setInputManualControl(true);

    const files = event.target.files;

    const fileList = Array.from(files);
    setArrayInput(fileList);

    const drop = document.getElementById("drop");
    drop.classList.add("hidden");
    const divider = document.getElementById("divider");
    divider.classList.add("lineTop");

    const paragraphs = fileList.map((file, index) => (
      <DivNameFile>
        <PNameFile key={index} className="text-break">
          {file.name}
        </PNameFile>
        <div>
          {(() => {
            const file = fileList[index];
            const sizeInBytes = file.size;
            let size;
            let unit;

            if (sizeInBytes >= 1024 * 1024) {
              size = sizeInBytes / (1024 * 1024);
              unit = "MB";
            } else {
              size = sizeInBytes / 1024;
              unit = "KB";
            }

            return `${size.toFixed(2)} ${unit}`;
          })()}
        </div>
        <BtnFile
          type="button"
          onClick={() => {
            const drop = document.getElementById("drop");
            drop.classList.remove("hidden");
            const divider = document.getElementById("divider");
            divider.classList.remove("lineTop");
            removeFile(index);
          }}
        >
          <ImgFile src={Exclude} alt="Excluir arquivo" />
        </BtnFile>
      </DivNameFile>
    ));

    setNameOnInputFiles(paragraphs);
    setFileSizeNotify(true);
  }

  function removeFile(indexToRemove) {
    if (arrayInput.length < 1) {
      setNameOnInputFiles("");
      setInputManualControl(false);
      return;
    }

    const updatedFiles = arrayInput.filter((_, index) => index !== indexToRemove);
    setArrayInput(updatedFiles);

    const updatedParagraphs = updatedFiles.map((file, index) => (
      <DivNameFile key={index}>
        <PNameFile className="text-break">{file.name}</PNameFile>
        <BtnFile type="button" onClick={() => removeFile(index)}>
          <ImgFile src={Exclude} alt="Excluir arquivo" />
        </BtnFile>
      </DivNameFile>
    ));

    const drop = document.getElementById("drop");
    drop.classList.add("hidden");
    const divider = document.getElementById("divider");
    divider.classList.add("lineTop");

    setNameOnInputFiles(updatedParagraphs);
  }

